.delete-btn {
  float: right;
  color: #e74944;
}

.save-btn {
  float: right;
  /* color: #e74944; */
  padding: 5px;
  margin: 5px;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.footer {
  min-height: 100px;
  background: red;
  position: relative;
  text-align: center;
  padding: 10px;
  margin: 3px;
}

.footerSpan {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
  background: lightgrey;
  font-size: medium;
}

.blueSpan {
  color: blue;
}

.redSpan {
  color: red;
}

.yellowSpan {
  color: orange;
}

.greenSpan {
  color: green;
}
